.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background-color: #f0f0f0; /* Light gray background */
  border-bottom: 2px solid #ccc; /* Gray border bottom */
}

.output {
  padding: 20px;
  width: 70%;
  background-color: #fff; /* White background */
  border: 1px solid #ccc; /* Light gray border */
}

.welcome,
.user-info,
.user-type {
  padding: 10px;
  margin-bottom: 10px;
  color: #333; /* Dark text color */
}

.user-type {
  font-weight: bold;
  color: #008080; /* Dark teal color for user type */
}

.student-details {
  margin-top: 20px;
  padding: 20px;
  width: 70%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.student-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
  background-color: #f9f9f9;
  width: calc(50% - 20px);
  max-width: 300px;
}

.student-card h3 {
  margin-top: 0;
  font-size: 16px;
  color: #333; /* Dark text color */
}

.student-card p {
  margin: 5px 0;
  color: #666; /* Lighter text color */
}

.cont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #f0f0f0; /* Light gray background */
  border-top: 2px solid #ccc; /* Gray border top */
}
